.contract-detail-content-wrapper .from-state .cur-step-icon {
  display: none;
}
.contract-detail-content-wrapper .cur-state {
  color: #E6A23C;
}
.contract-detail-content-wrapper .cur-state .cur-step-icon {
  display: inline;
  margin-left: 3px;
  font-size: 14px !important;
}
.contract-detail-content-wrapper .operation-tag[data-action] {
  animation-name: slideInRight;
  animation-duration: 1s;
  animation-delay: 0.2s;
  color: #E6A23C;
  cursor: pointer;
  display: inline-block;
}
.contract-detail-content-wrapper .operation-tag[data-action] .cur-step-icon {
  margin-left: 3px;
  font-size: 16px !important;
}
.contract-detail-content-wrapper .active-state {
  color: #67c23a;
}
.contract-detail-content-wrapper .slideInRight {
  animation-name: slideInRight;
  animation-duration: 1s;
  animation-delay: 0.2s;
}
.contract-detail-content-wrapper .to-state {
  color: #409EFF;
}
@keyframes slideInRight {
  from {
    transform: translate3d(100px, 0, 0);
    visibility: visible;
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
